.page {
  max-width: min(30em, 100%);
  display: flex;
  flex-direction: column;
  padding-left: 1em;
  padding-right: 1em;
  margin: auto;
}

.logo {
  margin-top: 2em;
  align-self: center;
  margin-bottom: 1em;
}

.link-label {
  margin-bottom: 0.4em;
}

.link {
  margin-top: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  font-style: normal;
  font-size: 13px;
  line-height: 118%;
  color: var(--light-foregrounds-regular);
  text-decoration: none;
  padding: 1rem 0 1rem 1rem;
}

.link a,
.link a:active,
.link a:visited {
  color: var(--light-foregrounds-primary);
}

.qr-and-params-container {
  margin-top: 1em;
  margin-bottom: 2em;
  display: flex;
  flex-direction: column;
  gap: 1em;
  word-wrap: break-word;
  align-items: stretch;
}

.qrcode-container {
  flex: 1;
  width: 100%;
  position: relative;
}

.params-container {
  flex: 1;
  width: 100%;
}

.param-row {
  display: flex;
  flex-direction: row;
}

.param-appendix {
  font-size: 13px;
}

b.param-appendix {
  font-family: 'Courier New', Courier, monospace;
}

.cursor-pointer {
  cursor: pointer;
}

.button {
  background-color: var(--light-backgrounds-inverse);
  color: var(--light-foregrounds-regular-inverse);
  border-radius: 100px;
  padding: 16px;
  min-width: 200px;
  border: none;
  text-decoration: none;
  font-size: 1rem;
  margin-top: 20px;
}

.button:hover {
  cursor: pointer;
}

.Main {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  flex: 3;
}

.rail {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.button-link {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  background: var(--light-backgrounds-inverse);
  color: var(--light-foregrounds-regular-inverse);
  border-radius: 40px;
  padding: 8px 16px;
  gap: 10px;
  box-sizing: border-box;
  text-decoration: none;
  font-size: 0.8125rem;
  font-family: 'GoodSans', system-ui, -apple-system, BlinkMacSystemFont,
    'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
    sans-serif;
}

.card {
  display: flex;
  flex-direction: column;
  background: var(--light-backgrounds-regular);
  border-radius: 1rem;
  overflow: hidden;
}

.card h3 {
  margin: 0;
}

.p-2 {
  padding: 2rem;
}

.icon-container {
  width: 4rem;
  height: 4rem;
  background-color: #eeeaff;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.deeplink-content {
  background: var(--light-backgrounds-regular);
  border-radius: 60px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  position: relative;
  border: 1px solid #cbcbca;
}

.copy-deeplink {
  background: var(--light-backgrounds-inverse);
  border-radius: 0px 31px 31px 0px;
  padding: 1rem 1.5rem;
  float: left;
}

.copy-deeplink:hover {
  cursor: pointer;
}

.w-50 {
  width: 50%;
}

@media (prefers-color-scheme: dark) {
  body {
    background: var(--dark-background-regular);
    color: var(--dark-foregrounds-regular);
  }

  body::-webkit-scrollbar {
    width: 10px;
  }

  body::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
  }

  body::-webkit-scrollbar-track:hover {
    background: rgba(255, 255, 255, 0.3);
  }

  body::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 6px;
    border: 2px solid transparent;
    margin: 2px;
    background-clip: padding-box;
  }

  body::-webkit-scrollbar-thumb:hover {
    background-color: rgba(255, 255, 255, 0.8);
  }

  a {
    color: var(--dark-foregrounds-regular);
    text-decoration: none;
  }

  .link {
    color: var(--dark-foregrounds-regular);
  }

  .link a,
  .link a:active,
  .link a:visited {
    color: var(--dark-foregrounds-primary);
  }

  .button,
  .button-link {
    background-color: var(--dark-backgrounds-inverse) !important;
    color: var(--dark-foregrounds-regular-inverse) !important;
  }

  .card {
    background-color: var(--dark-background-secondary);
  }

  .deeplink-content {
    background-color: var(--dark-background-secondary);
    border-color: var(--dark-background-secondary);
    color: var(--dark-foregrounds-regular);
  }
}
