.qr-code-container {
    margin: auto;
}

.qr-code {
    display: none;
    height: auto;
    margin: 0 auto;
    max-width: 1000px;
    width: 100%;
    padding: 15px;
    border-radius: 1em;
    background-color: #ffffff;
}

.qr-code.dark {
    background-color: #3F454D;
}

@media only screen and (min-width: 800px) {
    .open-mini {
        display: none;
    }
    .qr-code {
        display: flex;
    }
    .container {
        flex: 1;
    }
}
