:root {
  /* Light colors */
  --light-backgrounds-regular: #fff;
  --light-backgrounds-secondary: #f4f4ed;
  --light-backgrounds-inverse: #0f1721;
  --light-foregrounds-regular: #0f1721;
  --light-foregrounds-regular-inverse: #fff;
  --light-foregrounds-primary: #5433eb;
  /* Dark colors */
  --dark-background-regular: #0f1721;
  --dark-background-secondary: #3f454d;
  --dark-backgrounds-inverse: #fff;
  --dark-foregrounds-regular: #fff;
  --dark-foregrounds-regular-inverse: #0f1721;
  --dark-foregrounds-primary: #9c83f8;
}
@font-face {
  font-family: 'GoodSans';
  src: local('GoodSans'),
    url('https://cdn.shopify.com/static/fonts/GoodSans-Bold.woff2')
      format('woff2');
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

@font-face {
  font-family: 'GoodSans';
  src: local('GoodSans'),
    url('https://cdn.shopify.com/static/fonts/GoodSans-Medium.woff2')
      format('woff2');
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}

@font-face {
  font-family: 'GoodSans';
  src: local('GoodSans'),
    url('https://cdn.shopify.com/static/fonts/GoodSansText-Regular.woff')
      format('woff2');
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

html {
  font-family: 'GoodSans';
  font-size: 1em;
}

body {
  background: var(--light-backgrounds-secondary);
  color: var(--light-foregrounds-regular);
}

a {
  color: var(--light-foregrounds-regular);
  text-decoration: none;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
}

body::-webkit-scrollbar-track:hover {
  background: rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 6px;
  border: 2px solid transparent;
  margin: 2px;
  background-clip: padding-box;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

h1 {
  font-size: 1em;
  font-weight: 500;
}
